@import '../../styles/_colors';
@import '../../styles/_variables.scss';
@import '../../styles/_mixin';
@import '../../styles/_buttons';

.header{
    background-color: $backgroundColor;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2;
    -webkit-box-shadow: 0px 3px 7px -4px rgba(0,0,0,0.27);
    box-shadow: 0px 3px 7px -4px rgba(0,0,0,0.27);
}
.header__content{
    @include flex;
    height: 64px;
    max-width: $widthContent;
    a:hover{
        color: $primaryColor;
    }
}
.header__mobile_none{
    @include flex;
    margin: 0;
}

@import '../../styles/media';