// @mixin размеры основного контейнера
@mixin size-content{
    max-width: $widthContent; 
 }
 // @mixin настройки тектсов

//  заголовок h2
 @mixin h2-list {
    font-size: 2.57rem;
    font-family: 'Overpass';
    font-weight: bold;
    line-height: 51px;
    text-align: center;
 }
//  заголовок h3
 @mixin h3-list{
    font-family: 'Overpass';
    font-size: 1.625rem;
    line-height: 31px;
    letter-spacing: 0.1px;
    text-align: center;
 }

//  заголовок h4
@mixin h4-list{
   line-height: 22px;
   font-family: 'Overpass';
   font-size: 1.1rem;
   text-align: center;
}
//  Параграфы
 @mixin content-p{
    line-height: 22px;
 }
// inputs
@mixin registrationi-input {
   width: inherit;
   outline: none;
   border-style: initial;
   padding: 10px 0 18px 13px;
   font-size: 0.95rem;
}
// fieldset
@mixin registration-fieldset {
   border-radius: 4px;
   border-color: aliceblue;
   margin: 0;
}
@mixin flex{
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
}
@mixin grid {
   display: -ms-grid;
   display: grid;
}
@mixin flex-colum-reverse {
   -webkit-box-orient: vertical;
   -webkit-box-direction: reverse;
   -ms-flex-direction: column-reverse;
   flex-direction: column-reverse;
}
@mixin flex-colum{
   -webkit-box-orient: vertical;
   -webkit-box-direction: normal;
       -ms-flex-direction: column;
           flex-direction: column;
}