@import '../../../styles/_colors';
@import '../../../styles/_variables.scss';
@import '../../../styles/_mixin';
@import '../../../styles/_buttons';

.relationship__head{
    margin-top: 120px;
}
.relationship{
    h2{
        @include h2-list;
        width: 550px;
        padding-left: 18px;
        letter-spacing: 0.6px;
    }
} 
.relationship__content{
    @include flex;
    margin: 45px 0 138px 0;
    h3 {
        @include h3-list;
        margin: 20px 0 7px 0;
        text-align: inherit;
    }
}
.description__card__html{
    margin: 0 0 0 46px;
    p{
        width: 285px;
        line-height: 1.4;
        padding-left: 3px;
    }
    img{
        width: 98px;
    }
} 
.breakPoint_1,.breakPoint_2{
    display: none;
}
.description__card__css{
    margin: 0 0 0  36px;
    p{
        width: 240px;
        line-height: 1.4;
        margin-left: 3px;
        white-space: pre-line;
    }
    img{
        width: 98px;
    }
} 
.description__card__js{
    margin: 0 0 0 73px;
    p{
        width: 290px;
        line-height: 1.4;
        padding-left: 2px;
    }
    img{
        width: 99px;
    }
} 
@import '../../../styles/media';