@import '../../../../../styles/_buttons';
@import '../../../../../styles/_variables';
@import '../../../../../styles/_mixin';
@import '../../../../../styles/_colors';


// модальное окно **********************************************************************************
.popup_row{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0, 0.5);
    z-index: 99;
    .popup{
        position: absolute;
        width: 280px;
        left: 0;
        right: 0;
        top: 25%;
        background-color: $backgroundColor;
        padding: 20px;
        border-radius: 4px;
        h4{
            @include h4-list;
            text-align: left;
            font-size: 1.4rem;
            padding-bottom: 20px;
        }
        p{
            @include content-p;
            color: $colorParagraph;
            padding-right: 35px;
            padding-bottom: 30px;
        }
        span{
            display: block;
            width: 25px;
            margin-right: 0;
            color: $primaryColor;
            font-weight: bold;
            font-size: 1.2rem;
            cursor: pointer;
        }
        span:hover{
            color: $primaryColor_hover;
        }
    }
}

.popup_content{
    margin: 0;
    padding: 20px;
    h4{
        @include h4-list;
        text-align: left;
        font-size: 1.4rem;
        padding-bottom: 20px;
    }
    p{
        @include content-p;
        color: #111111c9;
        padding-right: 35px;
        padding-bottom: 30px;
    }
    span{
        display: block;
        width: 25px;
        margin-right: 0;
        color: $primaryColor;
        font-weight: bold;
        font-size: 1.2rem;
        cursor: pointer;
    }
    span:hover{
        color: $primaryColor_hover;
    }
}