@import '../../../styles/_variables.scss';
@import '../../../styles/_colors';
@import '../../../styles/_mixin';
@import '../../../styles/_buttons';


.requirements{
    max-width: $widthContent;
}
.requirements__bg {
    background-image: url(./img/third-block-bg/third-block-bg-1024x755.jpg);
    background-repeat: no-repeat;
}
.requirements__content{
    @include size-content;
    @include flex;
    @include flex-colum;
}
.requirements__head{
    h2{
        @include h2-list;
        color: $backgroundColor;
        font-weight: 400;
        padding-top: 135px;
        padding-left: 17px;
        letter-spacing: 0.6px;
    }
} 
.requirements_content__description{
    @include flex;
    margin: 0;
    padding-top: 38px;
    padding-bottom: 135px;
    padding-left: 45px;
    .description__text{
        width: 450px;
        padding: 0 3px;
        p{
            @include content-p;
            color: $backgroundColor;
        } 
    } 
}
.developer__working{
    margin-top: 22px;
    width: 448px;
}
.js_text{
    width: 450px;
    margin-top: 23px;
}
.description__img{
   margin: 16px 0 0 24px;
   background-image : url(./img/man-laptop-v1.svg);
   background-position: center center;
   background-size: contain;
   background-repeat: no-repeat;
   width: 100%;
   height: 360px;
}

@import '../../../styles/media';