@import '../../../styles/_colors';
@import '../../../styles/_variables.scss';
@import '../../../styles/_mixin';
@import '../../../styles/_buttons';


.bg_section{
    background-image: url(./img/bg-desktop.jpg);
    background-repeat: no-repeat;
}
.content__info{
    max-width: $widthContent;
    @include flex;
    @include flex-colum;
           h1{
            width: 500px;
            padding: 133px 0 0 46px;
            margin: 0;
            line-height: 60px;
            letter-spacing: 0.25px;
            color: $backgroundColor;
            font-family: $fontOverpas;
            font-weight: 600;
            font-size: 3.125rem;
        }
        p{
            width: 475px;
            padding: 18px 0 0 47px;
            color: $backgroundColor;
            font-size: 1.25rem;
            line-height: 1.65rem;
            letter-spacing: 0.03px;
            margin: 0;
        }
}
.info_btn{
    margin: 39px 0 160px 47px;
}
.info__btn{
    @include btn;
    background-color:$primaryColor;
    color: $backgroundColor;
}
.info__btn:hover{
    background-color: #fc831f;
}


@import '../../../styles/media';