@import './colors';
@import './variables';
// обнуление и центрирование
*{
    max-width: $widthLarge;
    margin:0 auto;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  body {
    font-size: 16px;
    font-family:'Source Sans Pro', sans-serif;
    font-weight:400;
  }
  a{
    text-decoration: none;
    cursor: pointer;
  }
  li{
    list-style: none;
  }
  section{
    max-width: $widthContent;
  }
  h1,h2,h3,h4,h5,h6,p{
    overflow: hidden;
    text-overflow: ellipsis;
  }
  p{
    color: $colorParagraph;
  }