@import '../../../../styles/_colors';
@import '../../../../styles/_variables.scss';
@import '../../../../styles/_mixin';
@import '../../../../styles/_buttons';

#registration__form{
    padding: 53px 48px 35px;
}
.form__data{
    @include flex;
    fieldset{
        @include registration-fieldset;
        width: 285px;
        margin-right: 31px;
        legend{
            font-size: small;
            padding: 0 5px;
            margin: 0px 8px;
            color: #a1a6ab;
        }
        input{
            @include registrationi-input;
        }
    }
}
.formInstruction__select{
    @include flex;
    margin-top: 39px;
    fieldset{
        @include registration-fieldset;
        width: 445px;
    }
    select{
        @include registrationi-input;
        padding: 18px 0 18px 13px;
        background-color: #ffffff;
    }
}

// inputErrors
.form__data_phone,
.form__data_name,
.form__data_email{
    color: $colorErrors;
}

// кастомизация псевдоэлемента(стрелочка)

.select {
    position: relative;
    display: inline-block;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  .select:after {
    content: url(./img/caret-down.svg);
    padding: 15px 8px;
    font-size: 1rem;
    position: absolute;
    right: 0;
    top: 1px;
    z-index: 1;
    text-align: center;
    width: 8%;
    pointer-events: none;
  }
  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

//  поле загрузки фото **************************************************************************
input[type="file" i]{
    display:none;
}
.uploadPhoto{
    span{
        color: #757575;
        font-size: 0.95rem;
        padding: 0px 0 0px 15px;
        font-family: 'Overpass';
    }
    fieldset{
        @include registration-fieldset;
        width: 450px;
        position: relative;
        margin-left: 30px;
        padding: 15px 0;
    }
    input{
        @include registrationi-input;
        padding: 15px 0;
    }
    strong{
        cursor: pointer;
        position: absolute;
        right: -2px;
        top: -2px;
        color: $primaryColor;
        font-size: 1.25rem;
        padding: 13px 33px;
        border: 2px solid $primaryColor;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
    }
}
.uploadPhoto__description {
    font-size: 0.765rem;
    text-align: right;
    padding-right: 152px;
    padding-top: 4px;
    color: $greyColor;
}
// кнопка ****************************************************************************************
.btnSignUp {
    margin-top: 41px;
    @include flex;
}     
.btnSignUp {
    button{
        background-color:$primaryColor;
        color: $backgroundColor;
        border: none;
        margin-left: 35%;
        font-family: 'Overpass';
    }
    button:hover{
        background-color:$primaryColor_hover;
    }
    button:disabled{
        background-color: $signUpForm_btn;
        color: $greyColor;
    }  
}


@import '../../../../styles/media';