@import './_mixin';
// ************************************************* ___desktop - 2K___ *************************************************
@media (min-width: 1025px) and( max-width:2560px) {
    // header ------------------------------------------------------------------------------------------------
    .logo{
       margin-left: 22px;
    }
    .header__content{
        @include grid;
        -ms-grid-columns: 1fr 2fr 1fr;
        grid-template-columns: 1fr 2fr 1fr;
    }
    .navMenu{
        margin-left: 25px;
    }
    .header__userLogin{
        margin: 1px 0px 0 20px;
    }
    // section Info------------------------------------------------------------------------------------------------
    .bg_section{
        background-image: url(../components/main/banner/img/bg-xl.jpg);
    }
    .bg_section,.requirements{
        max-width: inherit;
    }
    .content__info{
        h1,p{
            padding-left: 23px;
        }
    } 
    .content__info{
        span{
            display: block;
            width: 550px;
        }
    }
    .content__info{
        p{
            width: 490px;
            overflow: inherit;
        }
    } 
    .info_btn{
        margin: 35px 0 140px 25px;
    }
    // section aboutMe------------------------------------------------------------------------------------------------
    .aboutMe__head{
        margin-top: 135px;
        h2{
            letter-spacing: 0.6px;
        }
    }
    .aboutMe__img{
