        }
    }
    .aboutMe__btn{
        margin-top: 12px;
        margin-left: 36px;
    }
    // section relationship------------------------------------------------------------------------------------------------
    .breakPoint_3{
        display: none;
    }
    .relationship__head{
        margin-top: 74px;
    }
    .relationship__content{
        margin-bottom: 142px;
        h3{
            margin-top: 18px;
            margin-bottom: 8px;
        }
    }
    .description__card__html{
        margin: 0;
        padding-left: 23px;
        p{
            width: 345px;
            padding: 0;
        }
    } 
    .description__card__css{
        margin: 0;
        padding-left: 45px;
        p{
            width: 315px;
            margin-left: 0;
        }
    }
    .description__card__js{
        padding-left: 75px;
        margin: 0;
        p{
            width: 355px;
        }
    } 
    // section requirements------------------------------------------------------------------------------------------------
    .requirements__bg{
        background-image: url(../components/main/requirements/img/third-block-bg/third-block-bg-2560x692.jpg);
    }
    .requirements{
        h2{
            padding-left: 18px;
            padding-top: 132px;
        }
    }
    .requirements_content__description{
        padding: 38px 0 100px 25px;
        .description__text{
            margin: 0;
            width: 555px;
            padding: 0;
        }
        .description__img{
            margin: 7px 0px 0 23px;
            height: 363px;
        }
    }
    .developer__working,.js_text{
        width: inherit;
    }
    // section users------------------------------------------------------------------------------------------------
    .users__items_row{
        margin: 0;
        .item{
            margin-bottom: 29px;
        }
    }
    .users__btn{
        padding-top: 13px;
    }
    // section registration------------------------------------------------------------------------------------------------
    
    // form------------------------------------------------------------------------------------------------
    #registration__form{
        padding: 53px 0px 35px;
    }
    .form__data{
            @include grid;
            -ms-grid-columns: 1fr 1fr 1fr;
            grid-template-columns: 1fr 1fr 1fr;
            padding: 0 0 0 24px;
        fieldset{
            width: 355px;
             margin-right: 34px;
             legend{
                margin: -1px 10px;
             }
             input{
                padding: 10px 0 18px 14px;
             }
        }
    }
    .formInstruction__select{
        padding-left: 23px;
        select{
            width: 552px;
        }
    }
    .select:after{
        padding: 15px 0px;
        right: -3px;
    }
    .uploadPhoto{
        fieldset{
            width: 555px;
            padding: 16px 0;
            margin-left: 22px;
        }
        strong{
            padding: 14px 33px;
        }
    }
    .btnSignUp{
        button{
            margin-left: 38.5%;
        }
    }
    .uploadPhoto__description{
        text-align: left;
        padding-left: 39px;
    }
    // footer------------------------------------------------------------------------------------------------
    .wrapper_menu{
        margin-top: 115px;
    }
    .wrapper_menu__invert{
        padding: 7px 0;
        -ms-grid-columns: 2fr 1fr;
        grid-template-columns: 2fr 1fr;
    }
    .footer_navMenu{
        padding: 0;
        margin-right: -13px;
    }
    .contacts{
        margin: 0;
    }
    .footer_info{
        @include grid;
        -ms-grid-columns: 1fr 2fr;
        grid-template-columns: 1fr 2fr;
        padding-top: 25px;
    }
    .footer_navLink{
        margin: 0 7px 0 -5px;
    }
    .navLink_items{
        margin-right: 0;
    }
    .footer_bottom{
        -ms-grid-columns: 4fr 1fr;
        grid-template-columns: 4fr 1fr;
        padding-bottom: 27px;
    }
    .footer_bottom__left{
        margin: 0;
        p{
            padding: 4px 24px;
        }
    }
    .social{
        margin: 0 -3px;
        li{
            margin: 0 11px;
        }
    }
}
