@import '../../../styles/colors';
@import '../../../styles/mixin';

.footer_navLink{
    margin: 0 13px;
}
.navLink_items{
    @include grid;
    -ms-grid-columns: 1fr 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-right: 18px;
    li{
        list-style: none;
        margin: 0px 0px 10px 82px;
        text-align: right;
        a{
            color: #fff;
        }
        a:hover{
            color: $primaryColor;
        }
    }
}
@import '../../../styles/media';    