@import '../../../styles/_colors';
@import '../../../styles/_variables.scss';
@import '../../../styles/_mixin';
@import '../../../styles/_buttons';


.header__userLogin{
    @include flex;
    margin: 0;
}
.userName__description {
    margin-top: 14px;
    width: 165px;
    .nickName{
        font-size: 0.9rem;
        text-align: end;
        color: $blackColor;
    }
    .email{
        font-size: 0.75rem;
        text-align: end;
    }
}
.userAvatar {
    margin:12px 10px;
    img{
        border-radius: 50%;
        width: 40px;
    }
}
.logout_icon{
    margin-top: 22px;
    margin-left: 4px;
    @include flex;
} 
.logout_icon:hover .btn_logOut{
    fill: #2638bb;
}
@import '../../../styles/_media';