@import '../../styles/_colors';
@import '../../styles/_variables.scss';
@import '../../styles/_mixin';
@import '../../styles/_buttons';


footer{
    max-width: $widthLarge;
    background-color: $secondaryColor;
}
.footer_content{
    max-width:  $widthContent;
}
.wrapper_menu{
    margin-top: 105px;
}
.wrapper_menu__invert {
    padding: 8px 0;
    -webkit-filter: invert(1);
            filter: invert(1);
            @include grid;
    -ms-grid-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
}

.footer_logo{
    margin: 0;
}
.footer_navMenu{
    padding: 2px 10px 0 0;
    a:hover{
        -webkit-filter: invert(1);
                filter: invert(1);
        color: $primaryColor;
    }
}
#footer_line{
    border-bottom: 1px solid#878fc3;
}
.footer_info,.footer_bottom{
    @include flex;
    padding-top: 24px;
}
.footer_bottom{
    padding-top: 15px;
    padding-bottom: 21px;
    @include grid;
    -ms-grid-columns: 3fr 1fr;
    grid-template-columns: 3fr 1fr;
}
.footer_bottom__left{
    margin: 0 47px;
    p{
        padding: 10px 0px;
        font-size: 0.753rem;
        color: $backgroundColor;
    }
}

@import '../../styles/_media';