@import '../../../styles/_colors';
@import '../../../styles/_variables.scss';
@import '../../../styles/_mixin';
@import '../../../styles/_buttons';


.users__content{
    @include size-content;
    margin-top: 133px;
}
.users__head{
    h2{
        @include h2-list;
        color: black;   
        padding-bottom: 15px;
        padding-left: 18px;
        letter-spacing: 0.6px;
    }
    h4{
        @include h4-list;
        letter-spacing: -0.2px;
        padding-left: 15px;
    }
}
.users__items_row{
    @include grid;
    -ms-grid-columns: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 0 25px;
            .item{
                @include flex;
                margin: 56px auto 44px;
                margin-left: 23px;
                .item_img{
                    margin: 0;
                    img{
                        border-radius: 50%;
                        width: 70px;
                        height: 70px;
                    }
                }
            }
}
.item_description{
    padding-top: 24px;
    padding-left: 10px;
    p{
        font-size: 0.875rem;
        line-height: 20px;
        max-width: 205px;
        color: $colorParagraph;
    }
    h5{
        font-size: 1.4rem;
        font-weight: bold;
        line-height: 21px;
        max-width: 205px;
        white-space: nowrap;
    }
    .personal_data{
        padding-top: 14px;
    }
}
.users__btn{
    @include flex;
   padding-left: 16px;
   padding-top: 5px;
}
button{
    text-align: center;
    width: 290px;
    height: 40px;
    border: 2px solid $primaryColor;
    border-radius: 4px;
    background: $backgroundColor;
    color: $primaryColor;
    font-size: larger;
    font-weight: 600;
    outline: none;
    cursor: pointer;
}   
button:hover{
    background: #fee6d2;
}
@import '../../../styles/media';